/**
 * Base: Buttons
 */
.button {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
  text-decoration: none;
  font-weight: 400;
  font-family: 'Roboto', "Helvetica", "Arial", "sans-serif";
  -webkit-transition: background 0.2s;
  -o-transition: background 0.2s;
  transition: background 0.2s;
  padding: 12px 22px;
  border-radius: 100px;
  color: #363636;
  background: #336699;
  border: 1px solid transparent;
}
.button:focus,
.button:active:focus,
.button.active:focus,
.button.focus,
.button:active.focus,
.button.active.focus {
  outline: none;
}
.button:active,
.button.active {
  outline: 0;
}
.button.disabled,
.button[disabled] {
  cursor: disabled;
}
a.button {
  text-decoration: none;
}
a.button.disabled,
fieldset[disabled] a.button {
  pointer-events: none;
}
.button:hover,
.button:focus,
.button.focus {
  color: #363636;
  background: #264c73;
  text-decoration: none;
}
.button--secondary {
  color: #ffffff;
  background: #669933;
}
.button--secondary:hover,
.button--secondary:focus,
.button--secondary.focus {
  color: #ffffff;
  background: #4d7326;
}
.button--ghost {
  color: #669933;
  background: transparent;
  border-color: #669933;
}
.button--ghost:hover,
.button--ghost:focus,
.button--ghost.focus {
  color: #ffffff;
  background: #669933;
}
/**
 * Base: Form
 * General form field styling
 */
/* #input
    Common input field. Shared size and type resets for all input fields of the following:
    select,
    textarea,
    input of the type:
    - text
    - password
    - datetime
    - datetime-local
    - date
    - month
    - time
    - week
    - number
    - email
    - url
    - search
    - tel
    - color
********************/
.input {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 1;
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 100px;
  vertical-align: bottom;
}
.input:focus {
  outline: 0;
}
.input::-moz-placeholder {
  color: #d7d7d7;
  opacity: 1;
}
.input:-ms-input-placeholder {
  color: #d7d7d7;
}
.input::-webkit-input-placeholder {
  color: #d7d7d7;
}
.input::-ms-expand {
  border: 0;
  background-color: transparent;
}
.input[disabled],
.input[readonly],
fieldset[disabled] .input {
  background-color: #4b4b4b;
  opacity: 1;
}
.input[disabled],
fieldset[disabled] .input {
  cursor: disabled;
}
textarea.input {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: none;
}
input[type="radio"],
input[type="checkbox"] {
  margin: 4px 0 0;
  margin-top: 1px \9;
  line-height: normal;
}
input[type="file"] {
  display: block;
}
input[type="range"] {
  display: block;
  width: 100%;
}
select[multiple],
select[size] {
  height: auto;
}
input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
.radio,
.checkbox {
  position: relative;
  display: block;
  margin-top: 10px;
  margin-bottom: 10px;
}
.radio label,
.checkbox label {
  padding-left: 20px;
  margin-bottom: 0;
  font-weight: normal;
  cursor: pointer;
}
.radio input[type="radio"],
.radio-inline input[type="radio"],
.checkbox input[type="checkbox"],
.checkbox-inline input[type="checkbox"] {
  position: absolute;
  margin-left: -20px;
  margin-top: 4px \9;
}
.radio + .radio,
.checkbox + .checkbox {
  margin-top: -5px;
}
.radio-inline,
.checkbox-inline {
  position: relative;
  display: inline-block;
  padding-left: 20px;
  margin-bottom: 0;
  vertical-align: middle;
  font-weight: normal;
  cursor: pointer;
}
.radio-inline + .radio-inline,
.checkbox-inline + .checkbox-inline {
  margin-top: 0;
  margin-left: 10px;
}
input[type="radio"][disabled],
input[type="checkbox"][disabled],
input[type="radio"].disabled,
input[type="checkbox"].disabled,
fieldset[disabled] input[type="radio"],
fieldset[disabled] input[type="checkbox"] {
  cursor: disabled;
}
.radio-inline.disabled,
.checkbox-inline.disabled,
fieldset[disabled] .radio-inline,
fieldset[disabled] .checkbox-inline {
  cursor: disabled;
}
.radio.disabled label,
.checkbox.disabled label,
fieldset[disabled] .radio label,
fieldset[disabled] .checkbox label {
  cursor: disabled;
}
